@import "variables";
@import "typography";

html {
  font-family: 'Open Sans', Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1px;
  box-sizing: border-box; }

* {
  &, &:before, &:after {
    box-sizing: inherit; } }

body {
  margin: 0;
  padding: 0; }

.page {
  &-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    min-width: $page-min-width;
    background-color: $brand-color-1; }

  &-content {
    flex: 1; }


  &-footer {
    flex: 0 0; } }
